div.album {
    color: #003399;
    font-size: 20px;
    display: flex;
    align-items: center;
    width: 600px;
    height: 200px;
    margin: auto;
    margin-bottom: 10px;
    justify-content: center;
    padding: 10px 10px;
    border: 4px solid #3399FF;
    border-radius: 30px;
    background-color: white;

    li {
        display: flex;
        align-items: baseline;
        padding: 0;
        // border: 2px solid red

        h5 {
            padding: 0 10px 0 0;
            margin: 0;
            width: 60px;
            text-align: right; }

        h4 {
            margin: 10px;
            text-align: left;
            // flex-grow: 1
            width: 250px; } }

    .img {
        width: 120px;
        height: 120px;
        // background-color: grey
        margin: 20px;
        border-radius: 10px;
        overflow: hidden;
        box-shadow: 3px 3px 5px 0px rgba(0,51,153,1);

        img {
            width: 100%;
            height: 100%; } }

    a {
        position: absolute;
        align-self: flex-end;
        margin-bottom: 20px;
        opacity: 0.85;

        img {
            width: 40px;
            border: 2px solid #003399;
            border-radius: 50px; } }

    a:hover {
        opacity: 1;
        img {
            border: 2px solid #3399FF; } } }

@media (max-width: 700px) {
    div.album {
        font-size: 15px;
        max-width: 600px;
        min-width: 200px;
        width: 100%;
        max-height: 130px;
        padding: 5px 5px;

        li {
            h5 {
                padding: 0 5px 0 0;
                margin: 0;
                width: 40px; }

            h4 {
                margin: 5px;
                width: 120px; } }

        .img {
            width: 80px;
            height: 80px;
            margin: 20px; }

        a {
            img {
                width: 30px; } } } }

@media (max-width: 450px) {
    div.album {
        font-size: 10px;
        max-width: 600px;
        min-width: 200px;
        width: 100%;
        padding: 5px 5px;
        height: 100px;

        li {
            h5 {
                padding: 0 5px 0 0;
                margin: 0;
                width: 20px; }

            h4 {
                margin: 5px;
                width: 100px; } }

        .img {
            width: 50px;
            height: 50px;
            margin: 0px; }

        a {
            img {
                width: 20px; } } } }
